.loader {
  display: none;
}

.layout {
  opacity: 1;
}

.warning {
  display: none;
}

@media (prefers-color-scheme: light) {
  body {
    background-color: var(--document-color-light);
    color: var(--font-color-light);
    font-weight: 450;
  }
  .layout {
    background-color: var(--page-color-light);
  }
  main {
    background: url("/_assets/media/_shared/css/ooorganize.svg");
  }
  header {
    color: var(--h1-light);
  }
  header nav {
    border-top: 1px solid rgb(82, 70, 82);
    border-bottom: 1px solid rgb(82, 70, 82);
  }
  section {
    color: var(--font-color-light);
    border-color: rgb(190, 133, 141);
    background-color: var(--section-card-light);
    box-shadow: 10px 10px rgba(255, 149, 165, 0.5);
  }
  .warning {
    background-color: var(--warning-bg);
    box-shadow: 10px 10px rgba(131, 36, 131, 0.5);
  }
  .code {
    background-color: var(--button-code-txt);
    font-weight: 500;
  }
  h2 {
    color: var(--h2-light);
    border-color: rgb(233, 166, 205);
  }
  h3 {
    color: var(--h3-light);
  }
  a {
    color: var(--link-color-light);
  }
  a:hover {
    color: rgb(255, 255, 255);
    background-color: rgba(128, 0, 128, 0.637);
  }
  button {
    background-color: #f5e9f5;
    color: rgb(51, 44, 44);
  }
  button:hover {
    background-color: lightgoldenrodyellow;
  }
  #gif-controls button {
    background-color: var(--gif-controls-bg);
    color: var(--gif-controls-font);
    border: var(--gif-controls-border);
  }
  #gif-controls button:hover {
    background-color: var(--gif-controls-hover-bg);
  }
  #gif-controls button:active {
    background-color: var(--gif-controls-active-bg);
  }
  #gif-controls button:focus {
    background-color: var(--gif-controls-focus-bg);
  }
  footer {
    color: var(--footer-font);
  }
}
@media (prefers-color-scheme: dark) {
  body {
    color: white;
    background-color: black;
  }
  .layout {
    background-color: var(--page-bg-dark);
    border-color: var(--page-border-dark);
  }
  .page-columns {
    background: url("/_assets/media/_shared/css/ooorganize-dark.svg");
  }
  header {
    color: white;
  }
  header nav {
    border-top: 1px solid;
    border-bottom: 1px solid;
    border-color: var(--page-border-dark);
  }
  main {
    background: url("/_assets/media/_shared/css/ooorganize-dark.svg");
  }
  section,
  .warning {
    background-color: var(--section-card-dark);
    color: white;
  }
  section {
    box-shadow: 10px 10px rgba(104, 59, 84, 0.75);
    border-color: #665085;
  }
  .button-gallery {
    background-color: transparent;
  }
  h2 {
    border-color: rgb(110, 62, 90);
  }
  h2,
  h3 {
    color: var(--dark-mode-h);
  }
  a {
    color: var(--dark-mode-links);
  }
  a:hover {
    color: white;
  }
  button {
    background-color: var(--dark-button-bg);
    color: rgb(230, 214, 236);
  }
  button:hover {
    background-color: #712357;
  }
  button:active {
    background-color: rgb(207, 102, 102);
    color: white;
  }
  button:focus {
    background-color: #8b4f61;
  }
}